.mfa-page .twoFA .row1{
    margin:5% 0;
    padding:0 7.5%;
    h1{
        font-size: 32px;
        text-align: center;
    }
    .p{
        font-size: 20px;
        text-align: center;
        margin:15px 0 35px;
    }
    .items1{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .item1{
        width: 22.5%;
        border:1px solid transparent;
        border-radius: 10px;
        text-align: center;
        padding:15px 25px 25px;
        transition: 0.35s ease-in-out;

        &:hover{
            border:1px solid #eee;
            transform: translateY(-10px);
            .icon{
                background: #427cff;
                color:#fff;
            }
        }
    }
    .icon{
        transition: 0.2s ease-in-out;
        width: 75px;
        height: 75px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 45px;
        margin:0 auto;
        color:#427cff;
    }
    h6{
        font-size: 19px;
        margin:10px 0 17.5px;
    }
    .item1 p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .mfa-page .twoFA .row1{
        h1{
            font-size: 25px;
        }
        .p{
            font-size: 16px;
            margin:15px 0 30px;
        }
        .item1{
            width: 23%;
            padding:10px 20px 20px;
        }
        .icon{
            width: 65px;
            height: 65px;
            font-size: 40px;
        }
        h6{
            font-size: 16px;
            margin:10px 0 15px;
        }
        .item1 p{
            font-size: 15px;
        }
    }
}

@media(max-width:991px){
    .mfa-page .twoFA .row1{
        .p{
            margin:15px 0 20px;
        }
        .item1{
            width: 47.5%;
            margin:15px 0;
        }
    }
}

@media(max-width:550px){
    .mfa-page .twoFA .row1{
        margin:35px 0;
        h1{
            font-size: 18px;
        }
        .p{
            margin:10px 0;
        }
        .item1{
            width:100%;
            margin:10px 0;
            padding:10px 15px;
        }
    }
}

//=============================================
.mfa-page .twoFA .row2{
    margin:7.5% 0;
    padding:0 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    .info{
        width: 37.5%;
        p{
            font-size: 18px;
            line-height: 1.7;
        }
    }
    h1{
        font-size: 32.5px;
        margin-bottom: 20px;
    }
    .images{
        width: 45%;
        position: relative;
    }
    .img{
        content: url('../../../../assets/MFA/img1.png');
        width: 100%;
        border-radius: 15px;
    }
    .code{
        background: #fff;
        border-radius: 10px;
        position: absolute;
        width: 370px;
        left:0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        bottom: 0;
        padding:15px;
        p{
            font-size: 17px;
            text-align: center;
        }
        &:hover{
            .number{
                background: #427cff;
                color:#fff;
                border:1px solid #427cff;
            }
        }
    }
    .logo{
        content: url('../../../../assets/Navbar/blue-logo.png');
        width: 45px;
        margin:15px auto;
    }
    .circles{
        display: flex;
    }
    .circle{
        width: 13px;
        height: 13px;
        border-radius: 50%;
        background: #ccc;
        margin:0 2.5px;
    }
    .numbers{
        display: flex;
        justify-content: center;
        margin:35px 0;
    }
    .number{
        transition: 0.2s ease-in-out;
        width: 42px;
        height: 47px;
        margin:0 4px;
        border-radius: 5px;
        border: 1px solid #ddd;
        font-size: 27px;
        display: flex;
        align-items: center;
        justify-content: center;
        color:#555;
    }
    .btn{
        display: flex;
        justify-content: center;
    }
    button{
        background: #427cff;
        border:0;
        font-size: 17px;
        color:#fff;
        padding:12px 35px 10px;
        border-radius: 25px;
        width: fit-content;
        margin:0 auto;
        cursor: pointer;
    }
}

@media(max-width:1401px){
    .mfa-page .twoFA .row2{
        padding:0 7.5%;
        .info{
            width: 37.5%;
            p{
                font-size: 15px;
            }
        }
        h1{
            font-size: 25px;
            margin-bottom: 15px;
        }
        .images{
            width: 55%;
        }
        .code{
            width: 320px;
            p{
                font-size: 15px;
            }
        }
        .logo{
            width: 40px;
            margin:15px auto;
        }
        .circle{
            width: 11px;
            height: 11px;
        }
        .numbers{
            margin:25px 0;
        }
        .number{
            width: 35px;
            height: 40px;
            margin:0 3px;
            font-size: 20px;
        }
        button{
            font-size: 15px;
            padding:11px 35px 10px;
        }
    }
}

@media(max-width:991px){
    .mfa-page .twoFA .row2{
        flex-wrap: wrap;
        .info{
            width: 400px;
            max-width: 100%;
            margin:25px auto;
        }
        .images{
            width: 500px;
            max-width: 100%;
            margin:25px auto;
        }
        .code{
            padding:10px 15px;
            width: 250px;
        }
        .numbers{
            margin:20px 0;
        }
        .number{
            width: 32px;
            height: 36px;
            margin:0 2.5px;
            font-size: 17.5px;
        }
    }
}

@media(max-width:500px){
    .mfa-page .twoFA .row2{
        flex-wrap: wrap;
        padding:0;
        margin:10px 0;
        .info{
            max-width: 85%;
        }
        .images{
            max-width: 100%;
            margin:20px auto;
        }
        .img{
            border-radius: 0;
            width: 120%;
        }
        .code{
            padding:10px 15px;
            width: 250px;
        }
        .numbers{
            margin:20px 0;
        }
        .number{
            width: 32px;
            height: 36px;
            margin:0 2.5px;
            font-size: 17.5px;
        }
    }
}

//==========================================
.mfa-page .twoFA .leftright-reverse{
    padding:0 15% 0 0;
    margin:7.5% 0;
    .col1{
        width: 25%;
    }
    .other-elements{
        width: 55%;
    }
    .images{
        width: 100%;
        position: relative;
    }
    .top-img{
        content: url('../../../../assets/MFA/img2.png');
        width: 80%;
        margin-left: auto;
        position: relative;
        z-index: 2;
    }
    .bg-img{
        content: url('../../../../assets/MFA/bg2.png');
        position: absolute;
        width: 60%;
        left:0;
        top:0;
    }
}

@media(max-width:1401px){
    .mfa-page .twoFA .leftright-reverse{
        padding:0 10% 0 0;
        .col1{
            width: 30%;
        }
    }
}


@media(max-width:767px){
    .mfa-page .twoFA .leftright-reverse{
        padding:0;
        margin:30px 0;
        .col1{
            width: 85%;
            margin:20px auto;
        }
        .other-elements{
            width: 100%;
            margin:20px 0;
        }
        .top-img{
            margin-left: 7.5%;
        }
    }
}