.shared-pricing2{
    margin:7.5% 0;
    padding:0 7.5%;
    .h5{
        color:#427cff;
        font-size: 19px;
    }
    h1{
        font-size: 30px;
        margin:15px 0 85px;
    }
    .cards{
        display: flex;
        position: relative;
    }
    .img{
        position: absolute;
        content: url('../../../assets/Teams/pricing.png');
        right: 0;
        width: 50%;
        top: 50%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
    }
    .single-card{
        background: #fff;
        border:1px solid #e8e8e8;
        border-radius: 10px;
        width: 450px;
        margin-right: 3.5%;
        padding:65px 50px 30px;
        position: relative;
    }
    .logo{
        content: url('../../../assets/HowItWorks/blue-logo.png');
        position: absolute;
        width: 65px;
        left:0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        top:-40px;
    }
   
    .single-card h5{
        font-size: 30px;
        color:#333;
        text-align: center;
    }
    .price{
        margin: 15px 0 25px;
        text-align: center;
        span{
            font-size: 17px;
            color:#555;
        }
    }
    h6{
        color:#427cff;
        font-size: 55px;
        font-weight: normal;
        margin-bottom: 10px;
    }
    .p{
        font-size: 17.5px;
    }
    .features{
        margin: 25px 0 170px;
    }
    .feature-item{
        display: flex;
        align-items: center;
        margin:12.5px 0;
        svg{
            color:#427cff;
            font-size: 30px;
            margin-right: 20px;
        }
        p{
            flex: 1;
            font-size: 17px;
        }
    }
    .last-row{
        border-top:1px solid #ccc;
        padding:35px 0 0;
        position: absolute;
        bottom:30px;
        left:50px;
        right: 50px;
        display: flex;
        flex-direction: column;
        button{
            background: #427cff;
            color:#fff;
            border:0;
            cursor: pointer;
            margin-bottom: 22.5px;
            padding:15px;
            border-radius: 25px;
            font-size: 18px;
        }
        a{
            color:#555;
            font-size: 18px;
            text-align: center;
            &:hover{
                text-decoration: underline;
            }
        }
    }
}

@media(max-width:1401px){
    .shared-pricing2{
        .h5{
            font-size: 16px;
        }
        h1{
            font-size: 22.5px;
            margin:15px 0 75px;
        }
        .img{
            width: 50%;
        }
        .single-card{ 
            width: 370px;
            margin-right: 40px;
            padding:55px 40px 25px;
        }
        .logo{
            width: 55px;
            top:-35px;
        }
       
        .single-card h5{
            font-size: 22.5px;
        }
        .price{
            margin: 15px 0 25px;
            span{
                font-size: 14px;
            }
        }
        h6{
            font-size: 40px;
            margin-bottom: 8px;
        }
        .p{
            font-size: 15px;
        }
        .features{
            margin: 25px 0 140px;
        }
        .feature-item{
            margin:10px 0;
            svg{
                font-size: 25px;
            }
            p{
                font-size: 15px;
            }
        }
        .last-row{
            padding:30px 0 0;
            bottom:20px;
            left:40px;
            right: 40px;
            button{
                margin-bottom: 17.5px;
                padding:13px;
                font-size: 15px;
            }
            a{
                font-size: 15px;
            }
        }
    }
}

@media(max-width:1050px){
    .shared-pricing2{
        .cards{
            justify-content: space-between;
        }
        .single-card{ 
            width: 47.5%;
            margin:0;
        }
    }
}


@media(max-width:700px){
    .shared-pricing2{
        margin:30px 0 0px;
        h1{
            font-size: 20px;
            margin:15px 0 25px;
        }
        .cards{
            flex-wrap: wrap;
        }
        .single-card{ 
            width: 350px;
            max-width: 100%;
            margin:35px auto;
            padding:50px 30px 25px;

        }
        .img{
            display: none;
        }
    }
}

.shared-pricing2{
    .single-card{
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        
        transition: 0.35s ease-in-out;
        &:hover{
            transform: translateY(-25px);
        }
    }
    .single-card:before{
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0.5px;
        background: #427CFF;
        height: 5px;
        -webkit-transition-property: left, right;
        transition-property: left, right;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
        border-radius: 0 0 15px 15px;
    }
    .single-card:hover:before{
        left: 5px;
        right: 5px;
    }
}

@media(max-width:767px){
    .shared-pricing2{
        .single-card{
            &:hover{
                transform: translateY(0px);
            }
        }
    }
}